import React, { Component } from 'react';

import NavBar from './nav-bar';
import TabBar from './tab-bar';
import AddTask from './add-task';
import Task from './task';
import Group from './group';

import './app.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tasks: [],
      groups: [],
    };
  }

  addTask = (task) => {
    const { tasks } = this.state;
    tasks.push(task);
    this.setState({ tasks });
  };

  addGroup = (task) => {
    const { groups } = this.state;

    const group = Object.assign(task, {
      type: 'group',
      tasks: [],
    });

    groups.push(group);
    this.setState({ groups });
  };

  onDelete = (id) => {
    const { tasks, groups } = this.state;

    let index = tasks.findIndex((task) => task.id === id);
    if (index > -1) {
      tasks.splice(index, 1);
    } else {
      index = groups.findIndex((group) => group.id === id);
      if (index > -1) {
        groups.splice(index, 1);
      }
    }

    this.setState({ tasks, groups });
  };

  render() {
    const { tasks, groups } = this.state;

    return (
      <div className="app">
        <NavBar title="CREATE LIST" />
        <div className="content">
          <div className="task-list">
            {tasks.map(task => (
              <Task key={task.id} task={task} onDelete={this.onDelete} />
            ))}
          </div>
          <AddTask onSubmit={this.addTask} />
          <div className="group-list">
            {groups.map(group => (
              <Group key={group.id} group={group} onDelete={this.onDelete} />
            ))}
          </div>
          <AddTask prompt="Add New Group" onSubmit={this.addGroup} />
        </div>
        <TabBar />
      </div>
    );
  }
}

export default App;
