import React from 'react';
import ReactDOM from 'react-dom';

import './edit-task.css';

const domNode = document.getElementsByTagName('body')[0];

export default class EditTask extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: props.name,
    };

    this.input = React.createRef();
    this.modal = React.createRef();
  }

  componentDidMount() {
    this.input.current.focus();
  }

  onChange = (e) => {
    this.setState({name: e.target.value});
  };

  onKeyDown = (e) => {
    const { name } = this.state;
    if(e.keyCode === 13) {
      this.props.onChange(null, name);
    }
  };

  onClose = (e) => {
    if(e.target === this.modal.current) {
      this.props.onChange(e, null);
    }
  };

  render() {
    const { group } = this.props;
    const { name } = this.state;

    return ReactDOM.createPortal((
      <div className="edit-task-modal" onClick={this.onClose} ref={this.modal}>
        <div className="modal">
          <span className="mdi mdi-close" onClick={(e) => this.props.onChange(e, null)} />
          <h3>EDIT {group ? 'GROUP' : 'TASK'}</h3>
          <input
            ref={this.input}
            type="text"
            value={name}
            onChange={this.onChange}
            onKeyDown={this.onKeyDown}
          />
          <div className="actions">
            <a href="/" onClick={this.props.onDelete}>Delete</a>
            <a href="/" onClick={(e) => this.props.onChange(e, name)}>Save</a>
          </div>
        </div>
      </div>
    ), domNode);
  }
}
