import React from 'react';

import Task from './task';
import AddTask from './add-task';

import './group.css';
import EditTask from "./edit-task";

export default class Group extends React.Component {
  constructor(props) {
    super(props);
    this.state = Object.assign({
      editing: false,
    }, props.group);
  }

  addTask = (task) => {
    const { tasks } = this.state;
    tasks.push(task);
    this.setState({ tasks });
  };

  editTask = (e) => {
    e.preventDefault();
    this.setState({ editing: true });
  };

  onChange = (e, name) => {
    e.preventDefault();
    if(name !== null) {
      this.setState({name, editing: false});
    } else {
      this.setState({editing: false});
    }
  };

  onDelete = (e) => {
    const { id } = this.state;
    e.preventDefault();
    this.setState({ editing: false });
    this.props.onDelete(id);
  };

  render() {
    const { name, tasks, editing } = this.state;

    return (
      <div className="group">
        <div className="group-title">
          <span className="mdi mdi-pencil edit-group" onClick={this.editTask}></span>
          <h3>{name}</h3>
        </div>
        <div className="task-list">
          {tasks.map((task) => (
            <Task key={task.id} task={task} />
          ))}
        </div>
        <AddTask onSubmit={this.addTask} />
        {editing ? (
          <EditTask
            name={name}
            group
            onDelete={this.onDelete}
            onChange={this.onChange}
          />
        ): null }
      </div>
    );
  }
}
